import React, { useContext, useEffect, useState } from "react";
import "./no-sleep.css";
import NoSleepClass from "nosleep.js";
import { useLongPress } from "use-long-press";
import { GlobalContext } from "../../store/store";
import { resetAll } from "../../store/actions";

export default function NoSleep() {
  const noSleep = new NoSleepClass();
  const [isNoSleepActive, setNoSleepActive] = useState(noSleep.isEnabled);
  const dispatch = useContext(GlobalContext)[1];
  const bindLongPress = useLongPress(() => {
    dispatch(resetAll());
  });

  useEffect(() => {
    document.addEventListener(
      "click",
      async function enableNoSleep() {
        document.removeEventListener("click", enableNoSleep, false);
        await noSleep.enable();
        setNoSleepActive(noSleep.isEnabled);
      },
      false,
    );
    return () => {
      noSleep.disable();
      setNoSleepActive(noSleep.isEnabled);
    };
  }, []);

  return (
    <button {...bindLongPress} className="btn-clear no-sleep-btn">
      {isNoSleepActive ? "✅" : ""}
    </button>
  );
}
