/* eslint-disable @typescript-eslint/no-explicit-any */
import { isNullOrUndefined } from "../utils/is-null-or-undefined";
import randomNumber from "../utils/random-number";
import { ITimer } from "./types";

const LOCALSTORAGE_KEY = "__lukaskj.github.io(interval_timer)";

export const defaultTimers: ITimer[] = [
  { id: randomNumber(), time: 60 },
  { id: randomNumber(), time: 90 },
  { id: randomNumber(), time: 120 },
  { id: randomNumber(), time: 180 },
];

export function getSavedTimers(): ITimer[] {
  const savedTimersString = localStorage.getItem(LOCALSTORAGE_KEY) || "[]";
  let savedTimers: null | undefined | any;

  try {
    savedTimers = JSON.parse(savedTimersString);
  } catch (e) {
    savedTimers = [];
  }

  if (isNullOrUndefined(savedTimers) || !Array.isArray(savedTimers) || savedTimers.length === 0) {
    savedTimers = defaultTimers;
    saveTimers(defaultTimers);
  }

  return savedTimers as ITimer[];
}

export function saveTimers(timers: ITimer[]) {
  localStorage.setItem(LOCALSTORAGE_KEY, JSON.stringify(timers));
}
