import React, { createContext, Dispatch, useReducer } from "react";
import { Reducer } from "./reducer";
import { getSavedTimers } from "./timers";
import { IAction, IGlobalState } from "./types";

const initialState: IGlobalState = {
  time: 0,
  sets: 0,
  timers: getSavedTimers(),
};

// eslint-disable-next-line @typescript-eslint/no-empty-function
export const GlobalContext = createContext<[IGlobalState, Dispatch<IAction>]>([initialState, () => {}]);

export const Store: React.FC = ({ children }) => {
  const [state, dispatch] = useReducer(Reducer, initialState);
  return <GlobalContext.Provider value={[state, dispatch]}>{children}</GlobalContext.Provider>;
};
