/* eslint-disable @typescript-eslint/no-explicit-any */
// eslint-disable-next-line @typescript-eslint/no-unused-vars
import React, { useEffect, useRef } from "react";

export function useInterval<TArgs extends any[]>(callback: (...args: TArgs) => void, delay?: number): void {
  const savedCallback = useRef();

  // Remember the latest callback.
  useEffect(() => {
    savedCallback.current = callback as any;
  }, [callback]);

  // Set up the interval.
  useEffect(() => {
    function tick() {
      (savedCallback.current as any)();
    }
    if (delay !== null) {
      window.__TIMER_ID = setInterval(tick, delay);
      return () => clearInterval(window.__TIMER_ID);
    }
  }, [delay]);
}
