import { isNullOrUndefined } from "../utils/is-null-or-undefined";
import { Actions } from "./actions";
import { saveTimers } from "./timers";
import { IGlobalState, IAction } from "./types";

export const Reducer = (state: IGlobalState, action: IAction): IGlobalState => {
  switch (action.type) {
    case Actions.RESET:
      if (!isNullOrUndefined(action.payload) && !isNullOrUndefined(action.payload.timers)) {
        saveTimers(action.payload.timers);
      }
      return { ...state, ...action.payload };
    case Actions.ADD_SET:
      const addSet = action.payload?.sets || 0;
      let sets = state.sets + addSet;
      if (sets < 0) {
        sets = 0;
      }
      return { ...state, sets };
    case Actions.ADD_TIME:
      const addTime = action.payload?.time || 0;
      let time = state.time + addTime;
      if (time < 0) {
        time = 0;
      }
      return { ...state, time };

    case Actions.SET_TIME:
      let setTime = action.payload?.time || 0;
      if (setTime < 0) {
        setTime = 0;
      }
      return { ...state, time: setTime };
    case Actions.SET_TIME_BUTTON:
      if (isNullOrUndefined(action.payload) || isNullOrUndefined(action.payload.timers)) {
        return state;
      }
      const updatedTime = action.payload.timers[0];
      const updatedTimersList = state.timers.map((timer) => {
        if (timer.id === updatedTime.id) {
          return updatedTime;
        }
        return timer;
      });
      saveTimers(updatedTimersList);
      return { ...state, timers: updatedTimersList };
    default:
      return state;
  }
};
