import React, { useContext } from "react";
import { addSet } from "../../store/actions";
import { GlobalContext } from "../../store/store";
import { isNullOrUndefined } from "../../utils/is-null-or-undefined";

export function SetsButtons() {
  const [state, dispatch] = useContext(GlobalContext);
  return (
    <div className="sets_buttons__container">
      <button
        className="btn-clear"
        disabled={!isNullOrUndefined(state.sets) && state.sets <= 0 ? true : false}
        onClick={() => {
          dispatch(addSet(-1));
        }}
      >
        -
      </button>
      <span className="sets_buttons__divisor"></span>
      <button
        className="btn-clear"
        onClick={() => {
          dispatch(addSet(1));
        }}
      >
        +
      </button>
    </div>
  );
}
