import React, { useContext } from "react";
import { useLongPress } from "use-long-press";
import { addSet, setTime, setTimeButton } from "../../store/actions";
import { GlobalContext } from "../../store/store";
import { ITimer } from "../../store/types";
import { formatTime } from "../../utils/format-time";
import { isNullOrUndefined } from "../../utils/is-null-or-undefined";
import { IDefaultProps } from "../types";
import "./time-button.css";

interface TimeButtonProps {
  time: ITimer;
}

export function TimeButton({ time, className }: TimeButtonProps & IDefaultProps) {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [_, dispatch] = useContext(GlobalContext);
  function onButtonClick() {
    dispatch(setTime(time.time));
    dispatch(addSet(1));
  }

  function setNewTime() {
    const newTime = prompt("New time", String(time.time));
    if (isNullOrUndefined(newTime) || newTime.trim() === "") {
      return;
    }
    let seconds = 0;
    const matchColon = newTime.match(/([0-9]{2}):([0-9]{2})/g);
    const isNumber = Number.isInteger(parseInt(newTime));
    const newTimeNumber = parseInt(newTime);
    if (isNullOrUndefined(matchColon) && !isNumber) {
      return;
    }
    if (!isNullOrUndefined(matchColon)) {
      const numbers = newTime.split(":").map((i) => parseInt(i));
      seconds = numbers[0] * 60 + numbers[1];
    } else {
      if (isNumber && newTimeNumber > 0) {
        seconds = newTimeNumber;
      } else {
        return;
      }
    }
    dispatch(setTimeButton({ ...time, time: seconds }));
  }

  const bind = useLongPress(() => {
    setNewTime();
  });

  return (
    <button onClick={onButtonClick} {...bind} className={`btn-clear time-button__container noselect ${className}`}>
      {formatTime(time.time)}
    </button>
  );
}
