import React, { useContext } from "react";
import { resetSets, resetTime } from "../../store/actions";
import { GlobalContext } from "../../store/store";
import { Button } from "../button/button";
import NoSleep from "../no-sleep/no-sleep";
import { Sets } from "../sets/sets";
import { TimeButton } from "../time-button/time-button";
import { Timer } from "../timer/Timer";
import "./timer-layout.css";

export function TimerLayout() {
  const [state, dispatch] = useContext(GlobalContext);
  return (
    <div className="layout__container flex-column flex-center text-center">
      <NoSleep />
      <div className="layout__wrapper flex-column flex-wrap">
        <Timer />
        <div className="flex-row justify-space-around">
          <div className="layout-buttons flex-column justify-space-between w-50 margin-5">
            <Button onClick={() => dispatch(resetTime())}>Reset timer</Button>
            <Button onClick={() => dispatch(resetSets())}>Reset sets</Button>
          </div>
          <Sets className="w-50 margin-5" />
        </div>
        <div className="flex-row flex-wrap">
          {state.timers.map((time) => (
            <TimeButton key={time.id} time={time} className="flex-grow-1" />
          ))}
        </div>
      </div>
    </div>
  );
}
