import { defaultTimers } from "./timers";
import { IAction, ITimer } from "./types";

export enum Actions {
  LOADING,
  ADD_SET,
  ADD_TIME,
  SET_TIME,
  SET_TIME_BUTTON,
  RESET,
}

export function addSet(count: number): IAction {
  return {
    type: Actions.ADD_SET,
    payload: {
      sets: count,
    },
  };
}

export function addTime(count: number): IAction {
  return {
    type: Actions.ADD_TIME,
    payload: {
      time: count,
    },
  };
}

export function setTime(time: number): IAction {
  return {
    type: Actions.SET_TIME,
    payload: {
      time,
    },
  };
}

export function setTimeButton(newTime: ITimer): IAction {
  return {
    type: Actions.SET_TIME_BUTTON,
    payload: {
      timers: [newTime],
    },
  };
}

export function resetAll(): IAction {
  return {
    type: Actions.RESET,
    payload: {
      time: 0,
      sets: 0,
      timers: defaultTimers,
    },
  };
}

export function resetTime(): IAction {
  return {
    type: Actions.RESET,
    payload: {
      time: 0,
    },
  };
}

export function resetSets(): IAction {
  return {
    type: Actions.RESET,
    payload: {
      sets: 0,
    },
  };
}
